.item {
  position: relative;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background-color: black;
  overflow: hidden;
  -webkit-box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75); }

.image {
  width: 300px;
  height: 300px;
  transition: 0.5s; }

.name {
  position: absolute;
  bottom: 35px;
  left: 0;
  color: white;
  background-color: black;
  width: 100%;
  text-align: center;
  font-size: 20px; }

.name:hover {
  font-family: "rymF";
  color: #24bccc;
  -webkit-text-stroke: 0.01px #b4dc4c;
  cursor: pointer; }

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px; }

.image:hover {
  transform: scale(1.2);
  transition: 0.5; }

img {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "rymF";
  src: url("/src/fonts/rymF.ttf"); }

h1 {
  font-family: "rymF";
  text-align: center;
  color: #24bccc;
  -webkit-text-stroke: 0.01px #b4dc4c;
  font-size: 50px; }

.logo {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(/src/images/titulo.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-width: 338px;
  max-width: 682px;
  margin: 50px auto;
  height: 219px;
  padding: 5px;
  -webkit-box-shadow: 14px 13px 0px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 14px 13px 0px 1px rgba(0, 0, 0, 0.75);
  box-shadow: 14px 13px 0px 1px rgba(0, 0, 0, 0.75);
  border-top-left-radius: 100px;
  border-bottom-right-radius: 100px; }

h1 span {
  font-size: 90px; }

body {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/src/images/fondorym.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed; }

.search {
  width: 202px;
  margin: 0 auto;
  margin-bottom: 50px; }

.search p {
  color: white;
  font-weight: bold;
  text-align: center;
  font-size: 25px; }

.versus {
  min-width: 338px;
  max-width: 682px;
  margin: 50px auto;
  height: 219px;
  padding: 5px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 55px;
  align-items: center; }

.players {
  display: flex;
  gap: 10px;
  width: 270px;
  margin: 0 auto; }

.player-uno {
  width: 100px;
  height: 100px;
  background-image: url("/src/images/portal.png");
  -webkit-box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75);
  overflow: hidden;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover; }

.versus-logo {
  width: 50px;
  height: 50px; }

.versus-logo p {
  font-family: "rymF";
  text-align: center;
  color: #24bccc;
  -webkit-text-stroke: 2.009999999999991px #b4dc4c;
  font-size: 70px;
  margin: 0; }

.player-dos {
  width: 100px;
  height: 100px;
  background-image: url("/src/images/portal.png");
  -webkit-box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 0px 1px rgba(0, 0, 0, 0.75);
  overflow: hidden;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover; }

button {
  font-family: Arial, Helvetica, sans-serif;
  width: auto;
  background-color: #b4dc4c;
  border: 1px solid #076104;
  font-weight: bold;
  font-size: 40px;
  transition: 1; }

button:hover {
  transform: scale(1.1);
  transition: 1; }

.player-uno img {
  width: 100%;
  height: 100%; }

.player-dos img {
  width: 100%;
  height: 100%; }

.swal-button {
  background-color: #0aad05; }

.swal-button:not([disabled]):hover {
  background-color: #076104; }
